<template lang="pug">
  .cover
    Header
    h1.cover__title ARTAREA DIGITAL
    p.cover__descr
      | Продвижение товаров и услуг на рынке дизайнов интерьера и предметного дизайна.
      br
      | Разработка стратегических и креативных решений для успешного позиционирования.
    .cover__tip Прокрутите вниз
</template>

<script>
	import Header from "./Header";

	export default {
		name: 'Cover',
		components: {
			Header
		},
		data () {
			return {}
		},
    // com
	}
</script>

<style lang="sass">
  @import "../assets/_common/global"
  .cover
    width: 100%
    padding: 5vw 5vw 4vw 5vw
    height: 100vh
    background: url("../assets/bg-cover.png") center center no-repeat
    background-size: cover
    display: flex
    flex-direction: column
    justify-content: space-between
    align-items: flex-start
    &__title
      color: $color-white
      font-family: $font-SuisseIntl
      font-size: 72px
      font-weight: 600
      line-height: 65px
      text-transform: uppercase
      margin: auto 0 14px
    &__descr
      color: $color-white
      font-family: $font-SuisseIntl
      font-size: 20px
      font-weight: 600
      line-height: 32px
    &__tip
      opacity: 0.5
      color: $color-white
      font-family: $font-SuisseIntl
      font-size: 14px
      font-weight: 300
      line-height: 30px
      margin: 7vw 0 0 0
      padding: 0 0 0 31px
      position: relative
      &:before
        content: ''
        display: block
        width: 16px
        height: 9px
        background: url("../assets/icon-arrow-bottom.svg") center center no-repeat
        background-size: contain
        position: absolute
        left: 0
        top: 50%
        transform: translate3d(0,-50%,0 )
</style>



